.catalog {
  display: grid;
  justify-content: center;

  max-width: 944px;
  margin: 0 auto;
  gap: $card-gap;

  &__card {
    display: grid;
    justify-content: center;
    align-items: center;
    width: 200px;
    height: 300px;
    border: 1px solid #ccc;
    border-radius: 5px;
  }

}
